.overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.dialog {
  background: var(--secondary);
  border: 1px solid var(--border);
  border-radius: 1rem;
  width: 100%;
  max-width: 520px;
  max-height: 85vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation: slideUp 0.2s ease;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.25rem 1.5rem;
  border-bottom: 1px solid var(--border);
}

.header h2 {
  font-size: 1.25rem;
  font-weight: 600;
}

.closeBtn {
  background: transparent;
  border: none;
  color: var(--muted);
  font-size: 1.25rem;
  padding: 0.25rem;
  transition: color 0.15s;
}

.closeBtn:hover {
  color: var(--foreground);
}

.content {
  padding: 1.5rem;
  overflow-y: auto;
}

.stepDescription {
  color: var(--muted);
  margin-bottom: 1.5rem;
}

.projectList {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.projectCard {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid var(--border);
  border-radius: 0.5rem;
  text-align: left;
  transition: all 0.15s ease;
  width: 100%;
}

.projectCard:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--primary);
}

.projectCard.selected {
  background: rgba(124, 58, 237, 0.1);
  border-color: var(--primary);
}

.projectIcon {
  font-size: 1.5rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 0.375rem;
}

.projectInfo {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.projectName {
  font-weight: 500;
}

.projectMeta {
  font-size: 0.75rem;
  color: var(--muted);
}

.checkmark {
  color: var(--primary);
  font-size: 1.25rem;
}

.divider {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 1.5rem 0;
  color: var(--muted);
  font-size: 0.875rem;
}

.divider::before,
.divider::after {
  content: "";
  flex: 1;
  height: 1px;
  background: var(--border);
}

.openEditorBtn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 1rem;
  background: transparent;
  border: 2px dashed var(--border);
  border-radius: 0.5rem;
  color: var(--muted);
  transition: all 0.15s ease;
}

.openEditorBtn:hover {
  border-color: var(--primary);
  color: var(--primary);
  background: rgba(124, 58, 237, 0.05);
}

.formGroup {
  margin-bottom: 1.25rem;
}

.formGroup label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.formGroup input,
.formGroup textarea {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid var(--border);
  border-radius: 0.5rem;
  color: var(--foreground);
  font-size: 0.875rem;
  transition: border-color 0.15s;
}

.formGroup input:focus,
.formGroup textarea:focus {
  outline: none;
  border-color: var(--primary);
}

.formGroup textarea {
  resize: vertical;
  min-height: 80px;
}

.previewSection {
  margin-top: 1.5rem;
}

.previewSection h4 {
  font-size: 0.875rem;
  font-weight: 500;
  margin-bottom: 0.75rem;
  color: var(--muted);
}

.previewCard {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.02);
  border: 1px solid var(--border);
  border-radius: 0.5rem;
}

.previewThumbnail {
  width: 3.5rem;
  height: 3.5rem;
  background: linear-gradient(135deg, #1a1a2e, #16213e);
  border-radius: 0.375rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
}

.previewDetails {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.previewName {
  font-weight: 500;
}

.previewMeta {
  font-size: 0.75rem;
  color: var(--muted);
}

.footer {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem 1.5rem;
  border-top: 1px solid var(--border);
}

.footerSpacer {
  flex: 1;
}

.backBtn {
  padding: 0.625rem 1rem;
  background: transparent;
  border: 1px solid var(--border);
  border-radius: 0.5rem;
  color: var(--muted);
  font-weight: 500;
  transition: all 0.15s ease;
}

.backBtn:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--foreground);
}

.cancelBtn {
  padding: 0.625rem 1.25rem;
  background: transparent;
  border: 1px solid var(--border);
  border-radius: 0.5rem;
  color: var(--muted);
  font-weight: 500;
  transition: all 0.15s ease;
}

.cancelBtn:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--foreground);
}

.nextBtn,
.createBtn {
  padding: 0.625rem 1.25rem;
  background: var(--primary);
  border: none;
  border-radius: 0.5rem;
  color: white;
  font-weight: 600;
  transition: all 0.15s ease;
}

.nextBtn:hover:not(:disabled),
.createBtn:hover:not(:disabled) {
  background: var(--primary-hover);
}

.nextBtn:disabled,
.createBtn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
